import axios from 'axios'

const updateUser = async (id, user) => {
    let resp = await axios.put(`http://localhost:8001/users/${id}`, user);
    return resp.data;
}

const checkLogin = async (username, pwd) => {
    let valid = await axios.get(`http://localhost:8001/users/checkLogin/${username}/${pwd}`);
    return valid.data;
}

const userCreatedByAdmin = async (un, pwd) => {
    let resp = await axios.get(`http://localhost:8001/users/createdByAdmin/${un}/${pwd}`)
    return resp.data
}

const getFname = async (id) => {
    let fname = await axios.get(`http://localhost:8001/users/getFname/${id}`)
    return fname.data;
}

const getLoginData = async (id) => {
    let user = await axios.get(`http://localhost:8001/users/getLoginData/${id}`)
    return user.data;
}

const getAllUsersData = async () => {
    let users = await axios.get("http://localhost:8001/users/getAllUsersData")
    return users.data;
}

const getIndCompUserData = async (id) => {
    let user = await axios.get(`http://localhost:8001/users/getIndCompUserData/${id}`);
    return user.data;
}

const createUser = async (user) => {
    let resp = await axios.post("http://localhost:8001/users/", user);
    return resp.data;
}

const deleteUser = async (id) => {
    let resp = await axios.delete(`http://localhost:8001/users/${id}`);
    return resp.data;
}

const getAllUsersIDs = async () => {
    let ids = await axios.get("http://localhost:8001/users/getAllUsersIDs")
    return ids.data;
}

export default {
    checkLogin, userCreatedByAdmin, updateUser, getFname,
    getLoginData, getAllUsersData, createUser, getIndCompUserData,
    deleteUser, getAllUsersIDs
}